import React, {Component, PropTypes} from "react";
import {core, components} from 'react-starter';
const {navigate, dataLoader, SmartWrapper, Loader, MessageStack} = core;
const {Form, TextInput, Select} = components.Form;
const {List} = components.common;


class PlayerItem extends Component {
    render(){
        let itemData = this.props.itemData
        return <li>
            <div className="row">
                <div className="col-md-4">
                    <div className="user mb10">
                        <a href={"#/player/playerId="+itemData.id}>{itemData.firstName} {itemData.lastName} </a>
                    </div>
                </div>
            </div>
        </li>
    }
}

let sessionListConfig = {
    dataRequests: [{
        propKey: 'items',
        requestId: 'allPlayers'
    }]
}

class PlayerList extends Component {
    render() {
        return <div className="container">
            <SmartWrapper {...sessionListConfig}>
                <List ListItem={PlayerItem}></List>
            </SmartWrapper>
        </div>
    }
}

PlayerList.requiresSession = true;


export default PlayerList;


