import React, {Component, PropTypes} from "react";
import {core, components} from 'react-starter';
const {navigate, dataLoader, SmartWrapper, Loader, MessageStack} = core;
const {Form, TextInput, Select} = components.Form;
const {List} = components.common;


let playerListConfig = {
    dataRequests: [{
        propKey: 'items',
        requestId: 'allSessions',
        getParams: function () {
            return {playerId: this.props.playerId}
        }
    }]
}

class Session extends Component {
    render() {
        return <div className="uploader">
            <div className="container">
                <div className="row">
                    <div className="col-md-4">
                        <div className="user mb10">
                            User
                        </div>
                    </div>
                </div>
                <div className="row">
                    <div className="col-md-6">
                        <div className="video-area"/>
                    </div>
                    <div className="col-md-6">
                        <div className="image-area"/>
                    </div>
                </div>
            </div>
        </div>
    }
}

Session.requiresSession = true;


export default Session;


