let urlPrefix = 'api/spoblo/service/'

export default {
    login: {
        type: 'url',
        method: 'post',
        cache:'none',
        url: urlPrefix + 'login'
    },

    logout: {
        type: 'url',
        method: 'put',
        cache:'none',
        url: urlPrefix + 'logout'
    },

    allPlayers:{
        type: 'url',
        method:'get',
        cache:'none',
        url: urlPrefix + 'allPlayers',
        parser:function(resp){
            return resp.allPlayers;
        }
    },
    allSessions:{
        type: 'url',
        method:'get',
        cache:'none',
        url: urlPrefix + 'allSessions?playerId=:playerId',
        parser:function(resp){
            return resp.sessions;
        }
    },
    generateS3PreSignedUrlForPlayerVideo:{
        type: 'url',
        method:'get',
        cache:'none',
        url: urlPrefix + 'generateS3PreSignedUrlForPlayerVideo',
        parser:function(resp){
            return resp.url;
        }
    },

    userDetails: {
        type: 'static',
        cache:'none',
        data: {
            errors:['not logged in']
        },
        responseDelay: 100
    }
}






