import React, {Component, PropTypes} from "react";
import {core, components} from 'react-starter';
const {navigate} = core;

const {Form, TextInput, Select} = components.Form;


class Login extends Component {


    onSubmitHandler(data){
        if(data.userName==='test' && data.password==='test'){
            navigate('upload', {loggedIn:true});
        }
    }

    render() {
        return <div>

            <Form onSubmitHandler={this.onSubmitHandler.bind(this)}>
                <div className="row">
                    <div className="col-md-4">
                        <h1>Login</h1>
                        <TextInput name="oldPassword" type="password" placeholder="Old Password" label="Old Password"/>
                        <TextInput name="newPassword" type="password" placeholder="New Password" label="New Password"/>
                        <button className="btn btn-default">Change Password</button>
                    </div>
                </div>


            </Form>

            This is Login Page <a href="#/register"> register page </a>
        </div>
    }
}

export default Login;


