import React, {Component, PropTypes} from "react";
import {core, components} from 'react-starter';
import Dropzone from 'react-dropzone';
import 'whatwg-fetch';

const {navigate, dataLoader, SmartWrapper, Loader, MessageStack} = core;
const {Form, TextInput, Select} = components.Form;
const {List} = components.common;


let playerListConfig = {
    dataRequests: [{
        propKey: 'sessionList',
        requestId: 'allSessions',
        getParams: function () {
            return {playerId: this.props.playerId}
        }
    }]
}

class Uploader extends SmartWrapper {
    constructor() {
        super(...arguments);
        let selectionSession = this.props.sessionList[0];
        this.state = {
            selectionSession: selectionSession,
            files: []
        }
    }

    onDrop(files) {
        this.setState({
            files: [files[0]]
        });
    }

    startUpload(file) {
        this.addRequest('uploadUrl', 'generateS3PreSignedUrlForPlayerVideo', {playerId: this.props.playerId})
    }

    uploadFile(url) {
        let file = this.state.files[0];
        url = url.replace('https', 'http');
        var data = new FormData()
        data.append('file', file)
        fetch(url, {
            headers: {
                'Accept': 'application/json',
                'Content-Type': file.type
            },
            method: 'PUT',
            body: data
        })
            .then(function (resp) {
                console.log(resp)
            })
            .catch(function (e) {
                console.log(e);

            })


    }

    render() {

        if (this.state.loading) {
            return <div>uploading</div>
        }
        let selectedUser = this.state.selectionSession.user;
        if (this.dataIndex.uploadUrl) {
            this.uploadFile(this.dataIndex.uploadUrl);
        }
        return <div className="uploader">
            <div className="row">
                <div className="col-md-4">
                    <div className="user mb10">
                        <a href="javascript:void(0)">{selectedUser.firstName} {selectedUser.lastName} </a>
                    </div>
                </div>
            </div>
            <div className="row">
                <div className="col-md-6">

                    {this.state.files.length === 0 ? <Dropzone onDrop={this.onDrop.bind(this)} className="video-area">
                        Try dropping some files here, or click to select files to upload.
                    </Dropzone> : null}

                    {this.state.files.length > 0 ? <div>
                        <div>
                            {this.state.files.map((file) => <div>
                                <div>{file.name} </div>
                                <br/>
                                <br/>
                                <button className="btn btn-primary" onClick={this.startUpload.bind(this, file)}>Upload
                                </button>
                            </div>)}

                        </div>
                    </div> : null}
                </div>
            </div>
            {/*<div className="col-md-6">
             <div className="image-area"/>
             </div>*/}
        </div>
    }
}

class Player extends Component {
    render() {
        return <div className="container">

            <SmartWrapper {...playerListConfig} playerId={this.props.playerId}>
                <Uploader playerId={this.props.playerId}></Uploader>
            </SmartWrapper>

        </div>
    }
}

Player.requiresSession = true;


export default Player;


